import React, { useState,useEffect } from "react";

import { Text, Line, Img, Button } from "components";
import { useDispatch, useSelector } from "react-redux";
import { ADD_FETEMPLATES } from "store/slices/fullstack.slice";
import FETemplateButton from "components/FETemplateButton";
import { useNavigate } from "react-router-dom";
import ButtonNew from "components/ButtonNew";
import { ADD_FRONTEND_FETEMPLATES } from "store/slices/frontend.slice";
import FETemplateselectedModal from "modals/FETemplateselected";
import { makeApiRequest } from "services/Api/api";
import cloneDeep from 'lodash/cloneDeep';
import { SET_FRONTEND_APP_THEMENAME } from "store/slices/frontend.slice";

const FEScreen = ({path,selected_feTemplates}) => {
  const [selectedScreen, setSelectedScreen] = useState("");
  const[feSelectedScreenToRender,SetFeSelectedScreenToRender] = useState(false);
  const[feSelectedScreens,setFeSelectedScreens] = useState(selected_feTemplates);
  const[feActualScreenList,setFeActualScreenList] = useState([]);
  const [feScreenList,setFescreenList] = useState([]);
  const [UITemplateList,setUiTemplateList] = useState([]);
  const [SelectedUITemplate,SetSelectedUITemplate] = useState({});
  const [feSelectedPreviewScreens,setFeSelectedPreviewScreens] = useState([])
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const project_type = useSelector(store => store.basicInformation.projectType);
  const selected_FeTechnology = useSelector(store => store.frontend.frontend_details?.feTeckstack);

  useEffect(() => {
    ( async _ => {
      const uiTemplates = await makeApiRequest('GET',`/UITemplates?$filter=UITemplateMainTechnology eq ${selected_FeTechnology?.FeTechId}`);
       setUiTemplateList(uiTemplates?.value);      
    })()
  },[])
  useEffect(() => {
     ( async _ => {
     if(SelectedUITemplate){
      const screenListRes = await makeApiRequest('GET',`/UITemplates(${SelectedUITemplate.TemplateId})?$expand=AllScreens`);
      setFeActualScreenList(screenListRes?.AllScreens);  
      console.log(screenListRes.AllScreens);
      let screenList = cloneDeep(screenListRes?.AllScreens);
     const extractedList = await Promise.all(
      screenList.map( async (screen) => {
                const addedItem = screen;
                if(screen?.FrontendScreenScreenshotSmall != null){
                const smallImage = await makeApiRequest("GET",`/Documents(${screen?.FrontendScreenScreenshotSmall})/$value`);
                const finVal = smallImage.split('\n');
                addedItem.FrontendScreenScreenshotSmall = finVal[3];
                }
                if(screen?.FrontendScreenScreenshotBig != null){
                const bigImage = await makeApiRequest("GET",`/Documents(${screen?.FrontendScreenScreenshotBig})/$value`);
                const finVal = bigImage.split('\n');
                addedItem.FrontendScreenScreenshotBig = finVal[3];
                }
                console.log(addedItem);
                return addedItem;
               })
           )
               console.log( extractedList);
               setFescreenList(extractedList);          }
     })()
  },[SelectedUITemplate])
  const addToSelectedScreen = (screen) => {
    const screenToAdd = feActualScreenList.find(elem => elem?.FeScreenId === screen?.FeScreenId);
       setFeSelectedScreens((curList) => {
        return [...curList, screenToAdd];
    });
    setFeSelectedPreviewScreens((curList) => {
      return [...curList, screen];
    })
  };
  const removeScreenFromList = (screen) => {
    const filteredFeScreens = feSelectedScreens.filter((elem) => elem?.FeScreenId !== screen?.FeScreenId);
    setFeSelectedScreens(filteredFeScreens);
    const filteredPreviewScreens = feSelectedPreviewScreens.filter((elem) => elem?.FeScreenId !== screen?.FeScreenId);
    setFeSelectedPreviewScreens(filteredPreviewScreens);
  };
  const screenList = [
    {
      name: "Login",
      imgUrl: {
        small: "images/img_image62_1.png",
        large: "images/img_rectangle179.png",
      },
    },
    {
      name: "Signup",
      imgUrl: {
        small: "images/img_image62_2.png",
        large: "images/img_rectangle179.png",
      },
    },
    {
      name: "Product List",
      imgUrl: {
        small: "images/img_image62_3.png",
        large: "images/img_rectangle179.png",
      },
    },
    {
      name: "Product Details",
      imgUrl: {
        small: "images/img_image62_4.png",
        large: "images/img_rectangle179.png",
      },
    },
    {
      name: "Payment Detail",
      imgUrl: {
        small: "images/img_image62.png",
        large: "images/img_rectangle179.png",
      },
    },
    {
      name: "Checkout",
      imgUrl: {
        small: "images/img_image62_91x165.png",
        large: "images/img_rectangle179.png",
      },
    },
  ];
  const onsubmitHandler = () => {
    if(project_type === "Front End")
        dispatch(ADD_FRONTEND_FETEMPLATES(feSelectedScreens));
    else
      dispatch(ADD_FETEMPLATES(feSelectedScreens));
    navigate(path);
  }
  const deleteAllScreen = () => {
   setFeSelectedScreens([]);
   setFeSelectedPreviewScreens([]);
    }
  const onRequestClose = () => {
    SetFeSelectedScreenToRender(false)
  }
  const updateSelectedUITemplate = async (item) => {
    SetSelectedUITemplate(item);
    console.log(item);
    const selectedTheme = await makeApiRequest('GET',`/Themes(${item?.UITemplateTheme})`);
    console.log(selectedTheme)
    dispatch(SET_FRONTEND_APP_THEMENAME(selectedTheme));
  }
  return (
    <>  
      <FETemplateselectedModal 
       addedList={feSelectedPreviewScreens}
       feSelectedScreens={feSelectedScreens}       
       selectedScreen={selectedScreen} 
       removeScreenFromList={removeScreenFromList} 
       deleteAllScreen = {deleteAllScreen}
       onRequestClose ={onRequestClose}
       feSelectedScreenToRender={feSelectedScreenToRender}
       />
        <div className="flex flex-1 flex-col items-center justify-start md:px-[20px] w-[100%] pt-[32px]">
      <div className="flex flex-col items-center justify-start w-[100%]">
        <div className="flex items-start justify-start max-w-[982px] w-[100%]">
          <div className="flex flex-col gap-[20px] items-start justify-start w-[100%]">
            <div className="flex items-start justify-start w-[100%]">
              <div className="flex flex-col gap-[4px] items-start justify-start w-[100%]">
                <Text
                  className="font-medium text-gray_50 text-left w-[auto]"
                  as="h4"
                  variant="h4"
                >
                  Select Screen
                </Text>
                <Text
                  className="font-normal not-italic text-gray_501 text-left w-[auto]"
                  as="h6"
                  variant="h6"
                >
                  Choose the screens you want in your project
                </Text>
              </div>
            </div>
            <Line className="bg-gray_801 h-[1px] w-[100%]" />
          </div>
        </div>
        {/* mapping screen list */}
        <div className="flex md:flex-col flex-row gap-[17px] items-start justify-between w-[100%] max-h-[490px]">
          <div className="flex md:flex-1 items-center justify-start md:mt-[0] mt-[14px] md:w-[100%] w-[44%] h-[490px]">
            <div className="sm:gap-[20px] gap-[24px] grid sm:grid-cols-1 grid-cols-2 justify-center min-h-[auto] w-[100%] max-h-[490px] px-4 overflow-y-auto">
              {UITemplateList.map((item, index) => {
                return (
                  <>
                      <div className="flex flex-col gap-[20px] items-center justify-between w-[100%]" key={index}>
                        <div
                          onClick={() => updateSelectedUITemplate(item)}
                          className= {`${SelectedUITemplate.TemplateName === item?.TemplateName ? "bg-bluegray_906 border-[2px] border-solid border-teal_A400" : "bg-gray_901"} flex items-center justify-center h-[108px] rounded-[4px] w-[175px]`}
                        > 
                          <Img
                            src="https://cdn-icons-png.flaticon.com/128/4869/4869382.png"
                            className="h-[91px] m-[auto] rounded-[4px] w-[92%]"
                            alt="imageSixtyTwo_Two"
                          />                          
                        </div>
                        <Text
                          className="font-normal not-italic text-gray_300 text-left w-[auto]"
                          as="h5"
                          variant="h5"
                        >
                          {item.TemplateName}
                        </Text>
                      </div>
                    </>
                  );
                })}
              </div>
          </div>
          {/* showing expanded view for user selected screen on RHS */}
          {Object.keys(SelectedUITemplate).length === 0  ? (
            <div className="bg-gray_901 flex md:flex-1 items-center justify-start mb-[128px] p-[128px] sm:px-[20px] md:px-[40px] md:m-[0px] m-[14px] md:w-[100%] w-[56%] h-[490px]">
              <Text
                className="font-normal leading-[150.00%] my-[112px] not-italic text-center text-gray_501 w-[100%]"
                as="h6"
                variant="h6"
              >
                Select a Template to see itâ€™s capable Screens
              </Text>
            </div>
          ) : (
            <>
              <div className="flex flex-col md:gap-[40px] gap-[12px] items-center justify-start mb-[136px] mt-[14px] md:w-[100%] w-[56%] h-[490px]">
                <div className="flex flex-row sm:gap-[40px] items-end justify-between md:w-[100%] w-[96%]">
                  <Text
                    className="font-normal mb-[3px] mt-[8px] not-italic text-gray_50 text-left w-[auto]"
                    as="h5"
                    variant="h5"
                  >
                    {selectedScreen?.ScreenName}
                  </Text>
                  {!feSelectedScreens?.find(elem => elem?.FeScreenId === selectedScreen?.FeScreenId) ? (
                    <Button
                      onClick={() =>
                        addToSelectedScreen(selectedScreen)
                      }
                      className="border-[1px] border-solid border-teal_A400 cursor-pointer font-medium min-w-[115px] sm:px-[20px] px-[24px] py-[8px] rounded-[4px] text-[12px] text-center text-teal_A400 w-[auto]"
                    >
                      Add Screen
                    </Button>
                  ) : (
                    <Button
                      onClick={() =>
                        removeScreenFromList(selectedScreen)
                      }
                      className="border-[1px] border-solid border-teal_A400 cursor-pointer font-medium min-w-[138px] sm:px-[20px] px-[24px] py-[8px] rounded-[4px] text-[12px] text-center text-teal_A400 w-[auto]"
                    >
                      Remove Screen
                    </Button>
                  )}
                </div>
                <div className="flex items-center justify-start  md:w-[100%] w-[96%]">
                        <div className="flex md:flex-1 items-start justify-start md:mt-[0] mt-[14px] md:w-[100%] w-[100%] h-[450px]">
            <div className="sm:gap-[20px] gap-[24px] grid sm:grid-cols-1 grid-cols-2 justify-center min-h-[auto] w-[100%] max-h-[450px] px-4 overflow-y-auto">
              {feScreenList.map((item, index) => {
                return (
                  <>
                      <div className="flex flex-col gap-[20px] items-center justify-between w-[100%]" key={index}>
                        <div
                          onClick={() => setSelectedScreen(item)}
                          className={
                            feSelectedScreens?.find(elem => elem?.FeScreenId === item?.FeScreenId) ||
                            selectedScreen.FeScreenId === item?.FeScreenId
                              ? "bg-gray_901 flex items-center justify-center h-[108px] relative rounded-[4px] w-[175px]"
                              : "bg-gray_901 flex h-[108px] items-center justify-start p-[8px] rounded-[4px] w-[175px]"
                          }
                        > 
                          <Img
                            // src={item?.imgUrl?.small}
                            src={`data:image/jpeg;base64,${item?.FrontendScreenScreenshotSmall}`}
                            className="h-[91px] m-[auto] object-cover rounded-[4px] w-[92%]"
                            alt="imageSixtyTwo_Two"
                          />
                          {feSelectedScreens?.find(elem => elem?.FeScreenId === item?.FeScreenId) ? (
                            <div className="absolute bg-gray_900_66 flex h-[100%] inset-[0] items-center justify-center m-[auto] p-[74px] sm:px-[20px] md:px-[40px] rounded-[4px] w-[181px]">
                              <Img
                                src="images/img_close_teal_a400.svg"
                                className="cursor-pointer h-[32px] w-[32px]"
                                alt="close"
                                onClick={() =>
                                  removeScreenFromList(item)
                                }
                              />
                            </div>
                          ) : selectedScreen.FeScreenId === item?.FeScreenId ? (
                            <div className="absolute bg-gray_900_66 flex h-[100%] inset-[0] items-center justify-center m-[auto] p-[74px] sm:px-[20px] md:px-[40px] rounded-[4px] w-[181px]">
                              <Img
                                src="images/img_plus_teal_a400.svg"
                                className="cursor-pointer h-[32px] w-[32px]"
                                alt="plus"
                                onClick={() =>
                                  addToSelectedScreen(item)
                                }
                              />
                            </div>
                          ) : (
                            false
                          )}
                        </div>
                        <Text
                          className="font-normal not-italic text-gray_300 text-left w-[auto]"
                          as="h5"
                          variant="h5"
                        >
                          {item.ScreenName}
                        </Text>
                      </div>
                    </>
                  );
                })}
              </div>
          </div>
                </div>
              </div>
              {/* <div className="absolute bg-gray_901 flex items-center justify-start p-[11px] right-[0] top-[0] w-[60%]"> */}
                {/* below code is for showing the expanded view of screen */}
              {/* <div className="flex flex-col md:gap-[40px] gap-[88px] items-center justify-start mb-[136px] mt-[14px] md:w-[100%] w-[56%] h-[490px]">
                <div className="flex flex-row sm:gap-[40px] items-end justify-between md:w-[100%] w-[96%]">
                  <Text
                    className="font-normal mb-[3px] mt-[8px] not-italic text-gray_50 text-left w-[auto]"
                    as="h5"
                    variant="h5"
                  >
                    {selectedScreen?.ScreenName}
                  </Text>
                  {!feSelectedScreens?.find(elem => elem?.ScreenName === selectedScreen?.ScreenName) ? (
                    <Button
                      onClick={() =>
                        addToSelectedScreen(selectedScreen)
                      }
                      className="border-[1px] border-solid border-teal_A400 cursor-pointer font-medium min-w-[115px] sm:px-[20px] px-[24px] py-[8px] rounded-[4px] text-[12px] text-center text-teal_A400 w-[auto]"
                    >
                      Add Screen
                    </Button>
                  ) : (
                    <Button
                      onClick={() =>
                        removeScreenFromList(selectedScreen)
                      }
                      className="border-[1px] border-solid border-teal_A400 cursor-pointer font-medium min-w-[138px] sm:px-[20px] px-[24px] py-[8px] rounded-[4px] text-[12px] text-center text-teal_A400 w-[auto]"
                    >
                      Remove Screen
                    </Button>
                  )}
                </div>
                <div className="flex items-center justify-start  md:w-[100%] w-[96%]">
                  <Img
                    src= {`data:image/jpeg;base64,${selectedScreen?.FrontendScreenScreenshotBig}`}
                    className="h-[332px] md:h-[auto] object-cover rounded-[4px] w-[100%]"
                    alt="rectangle179"
                  />
                </div>
              </div> */}
              {/* </div>   */}
            </>
          )}
        </div>
      </div>
      {/* page navigation */}
      <div className="bg-gray_900 flex flex-col gap-[16px] h-[89px] md:h-[auto] inset-x-[0] items-center justify-start max-w-[982px] mt-[30px] mx-[auto] w-[100%]">
        <Line className="bg-gray_801 h-[1px] w-[100%]" />
        <div className="flex md:flex-col flex-row gap-[20px] items-start justify-start w-[100%]">
          <FETemplateButton
            className="flex sm:flex-1 flex-col items-center justify-center sm:px-[20px] md:px-[40px] px-[4px] py-[0px] rounded-[4px] sm:w-[100%] w-[220px]"
            text_One={`Selected Screens (${feSelectedScreens.length})`}
            onClick={() => SetFeSelectedScreenToRender(true)}
          />
          <div className="flex items-start justify-end w-[100%]">
          <div className="flex flex-row gap-[12px] items-center justify-center pr-2">
            <ButtonNew
             onClick={() => navigate(-1)}
             children="Previous"
            >
            </ButtonNew>
            <ButtonNew
              isActive={feSelectedScreens.length > 0}
              onClick={() =>
                feSelectedScreens.length > 0 ?
               onsubmitHandler() : null}
              children="Next"
              isSubmit
            >
            </ButtonNew>
          </div>
        </div>
        </div>
      </div>
    </div>       
    </>
  );
};

export default FEScreen;
